.no-padding {
  padding: 0px; }

.head {
  background-color: #2194f7;
  color: white;
  padding: 2em; }
  .head img {
    border-radius: 50%; }
    @media screen and (max-width: 767px) {
      .head img {
        max-height: 120px; } }
  .head p {
    padding: 5px;
